<template>
  <div class="index">
    <el-row>
      <el-row class="topNavList" :style="positionStyle">
        <el-row class="nav" @click.native="goAnchor('me')">个人简介</el-row>
        <el-row class="nav" @click.native="goAnchor('skill')">职业技能</el-row>
        <el-row class="nav" @click.native="goAnchor('job')">工作履历</el-row>
        <el-row class="nav" @click.native="goAnchor('contact')">联系方式</el-row>
      </el-row>
      <el-row class="topPic">
        <transition
            name="custom-classes-transition"
            enter-active-class="animated fadeInRight"
            leave-active-class="animated fadeInLeft">
          <el-row v-show="isShowAuthor">
            <el-row class="minePic" @click.native="goHome"></el-row>
            <el-row class="myName mt-15 font-size--large">Mr.wang</el-row>
            <el-row class="sub_text">别看不起小事，每一段艰难都是磨练。</el-row>
          </el-row>
        </transition>
      </el-row>
      <el-row class="layout-center">
        <el-row class="main text-center">
          <span class="main_text"> Every day is a good day </span>
        </el-row>
        <el-row ref="me" class="me">
          <el-row class="pt-20 pb-20 pl-40">个人简介</el-row>
          <el-row class="text-center" style="height: 500px; line-height: 500px; border: 1px solid #2d8cf0;">这里是我的个人信息
          </el-row>
        </el-row>
        <el-row ref="skill" class="skill">
          <el-row class="pt-20 pb-20 pl-40">职业技能</el-row>
          <el-row class="text-center" style="height: 500px; line-height: 500px; border: 1px solid #2d8cf0;">这里是我的职业技能
          </el-row>
        </el-row>
        <el-row ref="job" class="job">
          <el-row class="pt-20 pb-20 pl-40">工作履历</el-row>
          <el-row class="text-center" style="height: 500px; line-height: 500px; border: 1px solid #2d8cf0;">这里是我的工作履历
          </el-row>
          <el-row>海石科技 => 雅和科技 => 汇教慧学</el-row>
        </el-row>
        <el-row ref="contact" class="contact font-size--large pl-40" style="font-weight: bold;">联系方式</el-row>
        <el-row style="height: 500px; border: 1px solid #2d8cf0;">
          <el-row class="three-link" style="margin-top: 250px;">
            <transition
                name="custom-classes-transition"
                enter-active-class="animated fadeInRight"
                leave-active-class="animated fadeInLeft">
              <!--<el-row v-show="isShowLink" class="p-20 font-size&#45;&#45;large animated rotateIn">联系方式</el-row>-->
              <el-row v-show="isShowLink" type="flex" justify="space-around">
                <el-row class="icon1">
                  <el-row class="icon-pic1">
                    <el-image class="icon-pic" src="img/author/QQ.jpg"></el-image>
                    <el-row class="text-center p-10">扫一扫, 添加作者QQ</el-row>
                  </el-row>
                  <icon-svg
                      style="height: 80px;width: 80px"
                      name="qq"/>
                </el-row>
                <el-row class="icon2">
                  <el-row class="icon-pic2">
                    <el-image class="icon-pic" src="img/author/weChat.jpg"></el-image>
                    <el-row class="text-center p-10">扫一扫, 添加作者微信</el-row>
                  </el-row>
                  <icon-svg
                      style="height: 80px;width: 80px"
                      name="wechat"/>
                </el-row>
              </el-row>
            </transition>
          </el-row>
        </el-row>
      </el-row>
    </el-row>
  </div>
</template>

<script>
// 滚动监听
//https://www.npmjs.com/package/in-view
import inView from 'in-view'
import util from '@/libs/util.js'

export default {
  name: 'index',
  data () {
    return {
      isCollapse: true,
      isShowAuthor: true,
      isShowLink: false,
      positionStyle: {
        background: 'transparent'
      }
    }
  },
  created () {
    let a = window.location.href.split(':')[0]
    util.cookies.set(a + '-token', Math.random() * 10)
  },
  mounted () {
    inView('.topPic')
      .on('enter', () => {
        this.isShowAuthor = true
      })
      .on('exit', () => {
        this.isShowAuthor = false
      })
    inView('.contact')
      .on('enter', () => {
        this.isShowLink = true
      })
      .on('exit', () => {
        this.isShowLink = false
      })
    window.addEventListener('scroll', this.scrollPosition)
  },
  methods: {
    goHome () {
      this.$router.push({ name: 'home' })
    },
    scrollPosition () {
      // 获取滚动条位置
      let s = document.documentElement.scrollTop || window.pageYOffset || document.body.scrollTop
      if (s <= 10) {
        this.positionStyle.background = 'transparent'
      } else {
        this.positionStyle.background = 'linear-gradient(to top, #cfd9df 0%, #e2ebf0 100%)'
      }
    },
    goAnchor (selector) {
      let _this = this
      switch (selector) {
        case 'me':
          setTimeout(function () {
            _this.$refs.me.$el.scrollIntoView()
          }, 500)
          break
        case 'skill':
          this.$refs.skill.$el.scrollIntoView()
          break
        case 'job':
          this.$refs.job.$el.scrollIntoView()
          break;
        case 'contact':
          this.$refs.contact.$el.scrollIntoView()
          break
      }
    }
  },
  destroyed () {
    window.removeEventListener('scroll', this.scrollPosition)
  }
}
</script>
<style lang="scss">
.index {
  width: 100%;
  overflow-x: hidden;
  background: #f8f8f8;

  .el-row {
    position: static;
  }

  .topNavList {
    position: fixed;
    top: 0;
    z-index: 99;
    width: 100%;
    display: flex;
    justify-content: space-between;
    font-size: 26px;
    font-weight: bold;

    .nav {
      width: 120px;
      height: 60px;
      line-height: 60px;
      cursor: pointer;
    }
  }

  .topPic {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 100vh;
    background-image: url("https://api.dujin.org/bing/1366.php");
    background-size: cover;
    background-position: center;

    .minePic {
      width: 100px;
      height: 100px;
      border-radius: 50%;
      overflow: hidden;
      cursor: pointer;
      background-image: url("~@/../public/img/author/author.jpg");
      background-size: cover;
      background-position: center;
    }

    .myName {
      color: #fff;
      font-size: 60px;
      font-weight: 600;
      letter-spacing: 3px;
    }

    .sub_text {
      font-size: 21px;
      font-weight: 300;
      color: #fff;
      letter-spacing: 6px;
      margin-top: .5em;
      margin-bottom: .5em;
      background-image: -webkit-gradient(linear, left 0, right 0, from(#667eea), to(#764ba2));
      -webkit-background-clip: text; /*必需加前缀 -webkit- 才支持这个text值 */
      -webkit-text-fill-color: transparent; /*text-fill-color会覆盖color所定义的字体颜色： */
    }
  }

  .main {
    margin-top: 10px;
    height: auto;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    background-image: url("https://api.dujin.org/pic/");
    background-size: cover;
    background-position: center;
    padding: 20px 0;

    .main_text {
      text-transform: capitalize;
      font-size: 50px;
      font-weight: 600;
      background-image: -webkit-gradient(linear, left 0, right 0, from(#667eea), to(#764ba2));
      -webkit-background-clip: text; /*必需加前缀 -webkit- 才支持这个text值 */
      -webkit-text-fill-color: transparent; /*text-fill-color会覆盖color所定义的字体颜色： */
      font-family: Arial, Helvetica, sans-serif;
      color: rgba(174, 51, 226, 0.976);
    }
  }

  .three-link {
    padding: 20px 0;

    .icon-pic {
      /*width: 300px;*/
      /*height: 200px;*/
    }

    .icon1, .icon2 {
      position: relative;
    }

    .icon-pic1, .icon-pic2 {
      top: -404px;
      left: -90px;
      position: absolute;
      display: none;
      padding: 10px;
      width: 260px;
      background-image: linear-gradient(to top, #a18cd1 0%, #fbc2eb 100%);
    }

    .icon-pic2 {
      top: -310px;
    }

    .icon-pic1::after, .icon-pic2::after {
      content: '';
      position: absolute;
      width: 0;
      height: 0;
      border-left: 10px solid transparent;
      border-right: 10px solid transparent;
      border-top: 10px solid rgba(161, 140, 209, 1);
      margin-left: -10px;
      left: 50%;
      bottom: -10px;
    }

    .icon1:hover {
      .icon-pic1 {
        display: block;
      }
    }

    .icon2:hover {
      .icon-pic2 {
        display: block;
      }
    }
  }
}
</style>
